$breakpoint-mobile: 650px;

.container-post{
    width: 300px;
    height: 300px;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    border-radius: 10px;
    cursor: pointer;
}

@media (max-width: $breakpoint-mobile) {
    .container-post{
        width: 100%;
    }
}


